import { Grid } from '@material-ui/core';
import { gql, useMutation, ApolloError } from '@apollo/client';
import _get from 'lodash.get';
import { generatePath, useHistory } from 'react-router-dom';
import * as SurveyCreator from 'survey-creator';
import { memo, useEffect } from 'react';
import MainCard from '../../../ui-component/cards/MainCard';
import { createForm } from '../../../graphql/mutations';
import { gridSpacing } from '../../../store/constant';
import { FormType } from '../../../types';
import { useSnackBar } from '../../../hooks';
import { createFormCache } from '../../../dataProvider/cache';
import Form from './components/Form';

const FormAdd = () => {
	const history = useHistory();

	const options = { showLogicTab: true, showState: true };
	const surveyCreator = new SurveyCreator.SurveyCreator(null, options);
	const showSnackBar = useSnackBar();
	const [doCreate] = useMutation(gql(createForm), {
		update: createFormCache,
		onCompleted: data => {
			showSnackBar({
				message: 'Form has created',
				alertSeverity: 'success',
			});
			const formId = _get(data, ['createForm', 'FormId']);
			const formNewLink = generatePath('/form/:id', {
				id: formId,
			});
			history.push(formNewLink);
			// we need to reload this page because knockout js has some problem when we re-render template
			window.location.reload(true);
		},
		onError: (error: ApolloError) => {
			showSnackBar({
				message: _get(error, 'message', 'Something went wrong'),
				alertSeverity: 'error',
			});
		},
	});

	const onSubmitHandler = (values: FormType) => {
		const { formTypeId: categoryAndFormType, title } = values;
		const [categoryId, formTypeId] = categoryAndFormType.split(',');
		const template = JSON.stringify(surveyCreator.JSON);

		// save current template for caching
		sessionStorage.setItem('template', template);

		doCreate({
			variables: {
				Form: {
					CategoryId: categoryId,
					FormTypeId: formTypeId,
					Title: title,
					Template: template,
				},
			},
		});
	};

	const initialValues = {
		title: '',
		formTypeId: '',
		template: '',
	};

	useEffect(() => {
		sessionStorage.removeItem('template');
	}, []);

	return (
		<Grid container spacing={gridSpacing}>
			<Grid item xs={12}>
				<MainCard title='Add New Form' content={false}>
					<Form
						initialValues={initialValues}
						onSubmit={onSubmitHandler}
						surveyCreator={surveyCreator}
					/>
				</MainCard>
			</Grid>
		</Grid>
	);
};

export default memo(FormAdd);
