import {
	Button,
	Grid,
	Stack,
	TextField,
	Select,
	Divider,
	CardContent,
	CardActions,
} from '@material-ui/core';
import { Formik } from 'formik';
import { useQuery, gql } from '@apollo/client';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import AnimateButton from '../../../../../ui-component/extended/AnimateButton';
import SurveyComponent from '../SurveyCreator';
import Loader from '../../../../../ui-component/Loader';
import { listFormCategories } from '../../../../../graphql/queries';
import InputLabel from '../../../../../ui-component/extended/Form/InputLabel';
import { useSnackBar } from '../../../../../hooks';
import validationSchema from './Validation';
import { FormType } from '../../../../../types';

type Props = {
	initialValues: FormType;
	onSubmit: any;
	surveyCreator: any;
};

const Index = (props: Props) => {
	const { onSubmit, initialValues, surveyCreator } = props;
	const {
		loading: isFormTypeLoading,
		error: isFormTypeError,
		data: formCategories,
	} = useQuery(gql(listFormCategories));

	const showSnackBar = useSnackBar();

	if (isFormTypeLoading) return <Loader />;
	if (isFormTypeError) {
		showSnackBar({
			message: 'Something went wrong',
			alertSeverity: 'error',
		});
		return <></>;
	}

	if (_isEmpty(formCategories)) return <></>;
	const formTypes = _get(formCategories, 'listFormCategories');
	const { template } = initialValues;

	return (
		<Formik
			initialValues={initialValues}
			validationSchema={validationSchema}
			onSubmit={onSubmit}
		>
			{({ errors, handleChange, handleBlur, handleSubmit, touched, values }) => (
				<form noValidate onSubmit={handleSubmit}>
					<CardContent>
						<Grid container spacing={2} alignItems='center'>
							<Grid item xs={12} lg={6}>
								<InputLabel>Title</InputLabel>
								<TextField
									defaultValue={values.title}
									fullWidth
									placeholder='Title'
									onChange={handleChange}
									onBlur={handleBlur}
									error={touched.title && Boolean(errors.title)}
									helperText={touched.title && errors.title}
									name='title'
								/>
							</Grid>
							<Grid item xs={12} lg={6}>
								<InputLabel htmlFor='formTypeId'>Form Type</InputLabel>
								<Select
									native
									id='formTypeId'
									defaultValue={values.formTypeId}
									onChange={handleChange}
									error={touched.formTypeId && Boolean(errors.formTypeId)}
									name='formTypeId'
								>
									{formTypes.map((data: any) => {
										const {
											Title: categoryName,
											FormTypes: types,
											id: categoryId,
										} = data;
										return (
											<optgroup label={categoryName} key={categoryId}>
												{types.map((formType: any) => {
													const { Name: name, id: typeId } = formType;
													const categoryIdAndFormTypeId = `${categoryId},${typeId}`;
													return (
														<option
															value={categoryIdAndFormTypeId}
															key={categoryIdAndFormTypeId}
														>
															{name}
														</option>
													);
												})}
											</optgroup>
										);
									})}
								</Select>
							</Grid>
							<Grid item xs={12} lg={12}>
								<SurveyComponent value={template} surveyCreator={surveyCreator} />
							</Grid>
						</Grid>
					</CardContent>
					<Divider />
					<CardActions>
						<Grid item xs={12}>
							<Stack direction='row' justifyContent='flex-end'>
								<AnimateButton>
									<Button color='primary' variant='contained' type='submit'>
										Save
									</Button>
								</AnimateButton>
							</Stack>
						</Grid>
					</CardActions>
				</form>
			)}
		</Formik>
	);
};

export default Index;
