import _isNumber from 'lodash.isnumber';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import { GET_DYNAMIC_VALUES_PARAMS, TITLE_FEES } from './Constants';

export { getMonetaryTotalAmount, triggerMonetaryData } from './SCARForm';

export const rounder = (params: number[]) => {
	const multiply = params.reduce(
		(accumulator: number, current: number) =>
			accumulator * (_isNumber(current) ? current : 1),
		1
	);
	return Math.round(multiply);
};

export const getTitleFee = (params: number[]) => {
	const listPrice = _isNumber(params[0]) ? params[0] : 0;
	const titleFeeItem = TITLE_FEES.filter(
		(item: { ListPrice: number }) => item.ListPrice <= listPrice
	).sort(
		(item1, item2) => _get(item2, 'ListPrice', 0) - _get(item1, 'ListPrice', 0)
	);
	if (_isEmpty(titleFeeItem)) return 0;
	return _get(titleFeeItem, '0.TitleFee', 0);
};

// eslint-disable-next-line func-names
export const resetQuestions = function (this: any, params: string[]) {
	params.forEach(param => {
		const field = this.survey.getQuestionByName(param);
		field.value = '';
	});
};

// Usage: Additional_Buyer_Name expression in SF form
export function getDynamicValues(this: any, params: string[]): string {
	if (_get(params, 'length', 0) < GET_DYNAMIC_VALUES_PARAMS) return '';
	const [parentName, childName] = params;

	const parentField = this.survey.getQuestionByName(parentName);
	if (_isEmpty(parentField.value)) return '';
	return parentField.value
		.map((value: Object) => _get(value, childName, ''))
		.join(', ');
}
