import { FC, useEffect, memo } from 'react';
import * as Survey from 'survey-knockout';
// eslint-disable-next-line import/extensions
import './setting.tsx';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { Widgets } from './Shared/Widgets';
import * as CreatorHelper from './Helpers/SurveyCreator';

Widgets(Survey);

interface SurveyProps {
	value: string;
	surveyCreator: any;
}

const SurveyComponent: FC<SurveyProps> = props => {
	const { surveyCreator, value } = props;
	if (surveyCreator === null) return <></>;

	const template =
		JSON.parse(sessionStorage.getItem('template') as string) || value;

	useEffect(() => {
		surveyCreator.showToolbox = 'left';
		surveyCreator.showPropertyGrid = 'right';
		surveyCreator.rightContainerActiveItem('toolbox');
		surveyCreator.isAutoSave = true;
		surveyCreator.showState = false;
		surveyCreator.onQuestionAdded.add((senders: any, options: any) => {
			const question = _get(options, 'question');
			CreatorHelper.setQuestionId(question);
		});
		surveyCreator.onPanelAdded.add(
			(senders: any, options: { elementsValue: Object[] }) => {
				CreatorHelper.onCloningPanelHandler(options);
			}
		);
		surveyCreator.onElementDoubleClick.add(
			// eslint-disable-next-line no-shadow
			(senders: any, options: { element: any }) => {
				CreatorHelper.fillEmptyQuestionId(options);
			}
		);

		surveyCreator.render('creatorElement');
	}, [surveyCreator]);

	useEffect(() => {
		if (_isEmpty(template)) return;
		if (_isEmpty(surveyCreator)) return;
		surveyCreator.JSON = template;
	}, [template]);

	return <div id='creatorElement' />;
};

export default memo(SurveyComponent);
